.overlay {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
    to left,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0) 60%
  );
}

.overlay {
  color: white;
  font-size: 1rem;
}

.overlay a {
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  pointer-events: initial;
}

@keyframes inAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes outAnimation {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
